.form-group {
    width: 100%;

    margin-bottom: var(--form-group-margin-bottom);
}

.form-group--error {
    padding-top: var(--form-group-padding-top--error);
    padding-right: var(--form-group-padding-right--error);
    padding-bottom: var(--form-group-padding-bottom--error);
    padding-left: var(--form-group-padding-left--error);

    border-top: var(--form-group-border-top--error);
    border-right: var(--form-group-border-right--error);
    border-bottom: var(--form-group-border-bottom--error);
    border-left: var(--form-group-border-left--error);

    & input:matches([type="text"], [type="email"], [type="password"], [type="tel"], [type="number"], [type="search"]),
    & textarea,
    & select,
    & .form-checkbox label:before,
    & .form-radio label:before {
        border-color: var(--colour-error);
    }
}

.form-group__error-message {
    color: var(--colour-error);
}


.form-label {
    display: block;

    padding: var(--form-label-padding);
}

.form-label__hint {
    display: block;

    color: var(--form-label__hint-color);
    font-size: var(--form-label__hint-font-size);
}

input:matches([type="text"], [type="email"], [type="password"], [type="tel"], [type="number"], [type="search"]) {
    padding: var(--input-padding);

    border: var(--input-border);
    border-radius: var(--input-border-radius);

    outline: none;

    @media (--bp-s-down) {
        width: 100%;
    }

    &:focus {
        border: var(--input-border-focus);
    }
}

textarea {
    padding: var(--input-padding);

    border: var(--input-border);
    border-radius: var(--textarea-border-radius);

    outline: none;

    @media (--bp-s-down) {
        width: 100%;
    }

    &:focus {
        border: var(--input-border-focus);
    }
}

select {
    height: var(--input-height);

    outline: none;

    border: var(--input-border);
    border-radius: var(--select-border-radius);

    -webkit-appearance: menulist-button;

    @media (--bp-s-down) {
        width: 100%;
    }
}

textarea,
select,
input:matches([type="text"], [type="email"], [type="password"], [type="tel"], [type="number"], [type="search"]) {
    @for $i from 1 to $grid-number-of-columns {
        &.column--$(i) {
            width: calc((100% / $(grid-number-of-columns)) * $i);
        }

        @each $size in $breakpoint-sizes {
            @media (--bp-$(size)-only) {
                &.column--$(i)-$(size) {
                    width: calc((100% / $(grid-number-of-columns)) * $i);
                }
            }
        }
    }
}

.form-checkbox,
.form-radio {
    display: table;

    height: var(--input-height);

    padding-top: calc((var(--input-height) - var(--body-line-height)) / 2);
    padding-bottom: calc((var(--input-height) - var(--body-line-height)) / 2);

    & input[type="checkbox"],
    & input[type="radio"] {
        visibility: hidden;
        position: absolute;
        left: -9999px;
    }

    & span {
        position: relative;
        display: block;

        padding-left: calc(var(--checkbox-size) + var(--checkbox-spacing));
    }

    & span:before {
        content: '';
        display: inline-block;

        position: absolute;
        top: 50%;
        left: 0;

        transform: translateY(-50%);
    }

    @media (--bp-s-down) {
        display: block;
    }
}

.form-checkbox {
    & span:before {
        height: var(--checkbox-size);
        width: var(--checkbox-size);

        border: var(--checkbox-border);
        border-radius: var(--checkbox-border-radius);
    }

    & input[type="checkbox"]:checked + span:before {
        background-image: var(--checkbox-bg-checked);
        background-repeat: no-repeat;
        background-position: center;
    }
}

.form-radio {
    & span:before {
        height: var(--radio-size);
        width: var(--radio-size);

        border: var(--radio-border);
        border-radius: var(--radio-border-radius);
    }

    & input[type="radio"]:checked + span:before {
        background-image: var(--radio-bg-checked);
        background-repeat: no-repeat;
        background-position: center;
    }
}
