@charset "UTF-8";

.c-alert {
    margin: var(--alert-margin);
    padding: var(--alert-padding);
    border: var(--alert-border);
    border-radius: var(--alert-radius);
    font-weight: 400;
    overflow: hidden;

    & a {
        color: inherit;
        text-decoration: underline;
    }

    & p {
        margin-top: 0;
        color: inherit;

        &:last-child {
            margin-bottom: 0;
        }
    }

    &.c-alert--success {
        border-color: var(--colour-success);
        background-color: color(var(--colour-success) alpha(-90%));
        color: var(--colour-success);
    }

    &.c-alert--error {
        border-color: var(--colour-error);
        background-color: color(var(--colour-error) alpha(-90%));
        color: var(--colour-error);
    }
}
