.row {
    display: flex;
    flex-wrap: wrap;
}

.row:not(.row--flex-s, .row--column) > .column {
    @media (--bp-s-down) {
        flex-basis: 100%;
    }
}

.row--align-top {
    align-items: flex-start;
}

.row--align-center {
    align-items: center;
}

.row--align-bottom {
    align-items: flex-end;
}

.row--justify-left {
    justify-content: flex-start;
}

.row--justify-center {
    justify-content: center;
}

.row--justify-between {
    justify-content: space-between;
}

.row--justify-around {
    justify-content: space-around;
}

.row--justify-right {
    justify-content: flex-end;
}

.row--stretch {
    align-items: stretch;
}

.row--column {
    flex-direction: column;
}

.row--gutter:not(.row--column) {
    margin-left: calc(calc(var(--grid-gutter-size) / 2) * -1);
    margin-right: calc(calc(var(--grid-gutter-size) / 2) * -1);

    & > .column {
        padding-left: calc(var(--grid-gutter-size) / 2);
        padding-right: calc(var(--grid-gutter-size) / 2);
    }
}

.row--gutter.row--column {
    margin-top: calc(calc(var(--grid-gutter-size) / 2) * -1);
    margin-bottom: calc(calc(var(--grid-gutter-size) / 2) * -1);

    & > .column {
        padding-top: calc(var(--grid-gutter-size) / 2);
        padding-bottom: calc(var(--grid-gutter-size) / 2);
    }
}

.row__separator {
    flex: 0 0 var(--separator-size);
    background-color: var(--separator-colour);
}

.column {
    flex: 1;
}

@for $i from 1 to $grid-number-of-columns {
    .column--$(i) {
        flex-basis: calc((100% / $(grid-number-of-columns)) * $i);
        flex-grow: 0;
    }

    @each $size in $breakpoint-sizes {
        @media (--bp-$(size)-only) {
            .column--$(i)-$(size) {
                flex-basis: calc((100% / $(grid-number-of-columns)) * $i);
                flex-grow: 0;
            }
        }
    }
}

.column--align-top {
    align-self: flex-start;
}

.column--align-center {
    align-self: center;
}

.column--align-bottom {
    align-self: flex-end;
}

.column--pull-left {
    order: -1;
}

.column--pull-right {
    order: 20;
}

@each $size in $breakpoint-sizes {
    @media (--bp-$(size)-only) {
        .column--pull-left-$(size) {
            order: -1;
        }

        .column--pull-right-$(size) {
            order: 20;
        }
    }
}
