body {
    color: var(--body-font-colour);
}

p {
    font-size: var(--body-font-size);
    font-family: var(--body-font-family);
    font-style: var(--body-font-style);
    font-weight: var(--body-font-weight);
    line-height: var(--body-line-height);
    color: var(--body-font-colour);
}

h1 {
    font-size: var(--h1-font-size);
    font-family: var(--h1-font-family);
    font-style: var(--h1-font-style);
    font-weight: var(--h1-font-weight);
    line-height: var(--h1-line-height);
    color: var(--h1-font-colour);
}

h2 {
    font-size: var(--h2-font-size);
    font-family: var(--h2-font-family);
    font-style: var(--h2-font-style);
    font-weight: var(--h2-font-weight);
    line-height: var(--h2-line-height);
    color: var(--h2-font-colour);
}

h3 {
    font-size: var(--h3-font-size);
    font-family: var(--h3-font-family);
    font-style: var(--h3-font-style);
    font-weight: var(--h3-font-weight);
    line-height: var(--h3-line-height);
    color: var(--h3-font-colour);
}

h4 {
    font-size: var(--h4-font-size);
    font-family: var(--h4-font-family);
    font-style: var(--h4-font-style);
    font-weight: var(--h4-font-weight);
    line-height: var(--h4-line-height);
    color: var(--h4-font-colour);
}

h5 {
    font-size: var(--h5-font-size);
    font-family: var(--h5-font-family);
    font-style: var(--h5-font-style);
    font-weight: var(--h5-font-weight);
    line-height: var(--h5-line-height);
    color: var(--h5-font-colour);
}

h6 {
    font-size: var(--h6-font-size);
    font-family: var(--h6-font-family);
    font-style: var(--h6-font-style);
    font-weight: var(--h6-font-weight);
    line-height: var(--h6-line-height);
    color: var(--h6-font-colour);
}

a {
    color: var(--link-colour);
    text-decoration: var(--link-decoration);

    &:hover {
        color: var(--link-colour-hover);
        text-decoration: var(--link-decoration-hover);
    }

    &:active {
        color: var(--link-colour-active);
        text-decoration: var(--link-decoration-active);
    }

    &:visited {
        color: var(--link-colour-visited);
        text-decoration: var(--link-decoration-visited);
    }
}


/* Text utilities */

@each $direction in (left, center, right) {
    .u-text--$(direction) {
        text-align: $direction;
    }

    @each $size in $breakpoint-sizes {
        @media (--bp-$(size)-only) {
            .u-text--$(direction)-$(size) {
                text-align: $(direction);
            }
        }
    }
}
