@charset "UTF-8";

table {
	width: 100%;
    clear: both;
    border-collapse: collapse;
    border-spacing: 0;

    &:after {
        display: table;
        content: ' ';
        clear: both;
    }
}

th {
    font-weight: bold;
    padding: var(--table-th-padding);
}

td {
    padding: var(--table-td-padding);
}

tr {
    border-bottom: var(--table-tr-border);
}

thead {
    border-bottom: var(--table-thead-border);
}

tfoot {
    border-top: var(--table-tfoot-border);
}

@media (--bp-s-only) {
    .table--responsive {
        & tr {
            display: block;
        }

        & td {
            display: block;
            width: 100%;
            padding-right: 0;

            &[data-responsive-label]:before {
                content: attr(data-responsive-label) "";
                float: left;
                margin-right: 10px;
                font-weight: bold;
            }
        }
    }
}
