@charset "UTF-8";

@define-mixin button $padding, $background, $background-hover, $background-active, $colour, $colour-hover, $colour-active, $border, $border-hover, $border-active, $radius, $decoration, $decoration-hover {
    position: relative;
    display: inline-block;
    padding: $padding;
    border: $border;
    border-radius: $radius;
    background: $background;
    color: $colour;
    font-size: inherit;
    font-smoothing: antialiased;
    text-decoration: $decoration;
    appearance: none;
    outline: none;
    cursor: pointer;

    &:hover {
        border: $border-hover;
        background: $background-hover;
        color: $colour-hover;
        text-decoration: $decoration-hover;
        opacity: 0.9;
    }

    &:active {
        border: $border-active;
        background: $background-active;
        color: $colour-active;
        opacity: 0.8;
    }

    &:visited {
        background: $background;
        color: $colour;
    }

    &.disabled,
    &[disabled] {
        opacity: 0.3;

        &:hover,
        &:active,
        &:visited {
            cursor: default;
            background: $background;
            color: $colour;
        }
    }

    @mixin-content;
}

.o-button {
    @mixin button
        var(--button-padding),
        var(--button-background),
        var(--button-background-hover),
        var(--button-background-active),
        var(--button-colour),
        var(--button-colour-hover),
        var(--button-colour-active),
        var(--button-border),
        var(--button-border-hover),
        var(--button-border-active),
        var(--button-radius),
        var(--button-decoration),
        var(--button-decoration-hover);
}

