        var(--button-link-colour-active),
        var(--button-link-border),
        var(--button-link-border-hover),
        var(--button-link-border-active),
        var(--button-link-radius),
        var(--button-link-decoration),
        var(--button-link-decoration-hover);
}

.o-button--block {
    display: block;
    text-align: center;
}

@each $size in $breakpoint-sizes {
    @media (--bp-$(size)-only) {
        .o-button--block-$(size) {
            display: block;
            text-align: center;
        }
    }
}

.o-button--small {
    padding: var(--button-small-padding);
}

.o-button--large {
    padding: var(--button-large-padding);
}

.o-button--square {
    border-radius: 0;
}
