@charset "UTF-8";

.c-tabs {
    width: var(--tabs-width);

    & .c-tabs__navigation {
        height: var(--tabs__navigation-height);
        width: var(--tabs__navigation-width);

        margin: var(--tabs__navigation-margin);
        padding: var(--tabs__navigation-padding);

        background-color: var(--tabs__navigation-background);
        border-radius: var(--tabs__navigation-radius);
        box-shadow: var(--tabs__navigation-boxshadow);

        & a {
            display: inline-block;

            margin: var(--tabs__navigation-a-margin);
            padding: var(--tabs__navigation-a-padding);

            color: var(--tabs__navigation-a-color);

            cursor: pointer;

            &.is-active {
                background-color: var(--tabs__navigation-a-background);
                border-radius: var(--tabs__navigation-a-radius);
                color: var(--tabs__navigation-a-color-active);
                box-shadow: var(--tabs__navigation-a-boxshadow);
            }
        }
    }

    & .c-tabs__content {
        &:not(.is-active) {
            display: none;
        }

        &.is-active {
            display: inherit;
        }
    }
}
