@charset "UTF-8";

.c-modal {
    position: fixed;
    top: var(--modal-top);
    right: var(--modal-right);
    bottom: var(--modal-bottom);
    left: var(--modal-left);
    overflow-x: hidden;

    z-index: var(--modal-z-index);

    &:not(.is-active) {
        display: none;
    }

    &.is-active {
        display: inherit;
    }


    &.c-modal__overlay {
        background: var(--modal__overlay-background);
    }

    & .c-modal__content {
        position: relative;

        max-width: 100%;
        width: var(--modal__content-width);
        height: var(--modal__content-height);

        margin: var(--modal__content-margin);
        padding: var(--modal__content-padding);

        border-radius: var(--modal__content-border-radius);

        background: var(--modal__content-background);

        box-shadow: var(--modal__content-box-shadow);

        @media (--bp-s-only) {
            min-height: 100vh;

            margin: var(--modal__content-margin-s);
            padding: var(--modal__content-padding-s);
        }
    }

    & .c-modal__close {
        position: absolute;
        top: var(--modal__close-top);
        right: var(--modal__close-right);
        bottom: var(--modal__close-bottom);
        left: var(--modal__close-left);

        height: var(--modal__close-height);
        width: var(--modal__close-width);

        line-height: var(--modal__close-height);
        text-align: center;

        @media (--bp-s-only) {
            position: fixed;
        }
    }
}
