@charset "UTF-8";

*,
*:before,
*:after {
    box-sizing: border-box;
}

html {
    background-color: var(--page-background);
    font-size: var(--root-font-size);
    -webkit-text-size-adjust: 100%;
}

body {
    background-color: var(--page-background);
    font-size: var(--body-font-size);
    line-height: var(--body-line-height);
}

.c-container {
    position: relative;
    width: var(--container-width-l);
    height: auto;

    margin: 0 auto;
    background-color: var(--page-foreground);

    overflow: hidden;

    @media (--bp-s-only) {
        width: var(--container-width-s);
        max-width: var(--container-width-s);
    }

    @media (--bp-m-only) {
        width: var(--container-width-m);
    }

    @media (--bp-xl-only) {
        width: var(--container-width-xl);
    }

    &.c-container--full-width {
        width: 100%;
        background-color: var(--page-foreground);
    }
}

img {
    max-width: 100%;
    height: auto;
}

iframe {
    max-width: 100%;
}


/* Margin & padding */

@each $num in $utility-sizes {
    .u-padding--$(num) {
        padding-top: $(num)px !important;
        padding-right: $(num)px !important;
        padding-bottom: $(num)px !important;
        padding-left: $(num)px !important;
    }
    .u-padding-y--$(num) {
        padding-top: $(num)px !important;
        padding-bottom: $(num)px !important;
    }
    .u-padding-x--$(num) {
        padding-left: $(num)px !important;
        padding-right: $(num)px !important;
    }

    .u-margin--$(num) {
        margin-top: $(num)px !important;
        margin-right: $(num)px !important;
        margin-bottom: $(num)px !important;
        margin-left: $(num)px !important;
    }
    .u-margin-y--$(num) {
        margin-top: $(num)px !important;
        margin-bottom: $(num)px !important;
    }
    .u-margin-x--$(num) {
        margin-left: $(num)px !important;
        margin-right: $(num)px !important;
    }

    @each $dir in (top, right, bottom, left) {
        .u-padding-$(dir)--$(num) {
            padding-$(dir): $(num)px !important;
        }
        .u-margin-$(dir)--$(num) {
            margin-$(dir): $(num)px !important;
        }
    }

    @each $size in $breakpoint-sizes {
        @media (--bp-$(size)-only) {
            .u-padding-$(size)--$(num) {
                padding-top: $(num)px !important;
                padding-right: $(num)px !important;
                padding-bottom: $(num)px !important;
                padding-left: $(num)px !important;
            }
            .u-padding-y-$(size)--$(num) {
                padding-top: $(num)px !important;
                padding-bottom: $(num)px !important;
            }
            .u-padding-x-$(size)--$(num) {
                padding-left: $(num)px !important;
                padding-right: $(num)px !important;
            }

            .u-margin-$(size)--$(num) {
                margin-top: $(num)px !important;
                margin-right: $(num)px !important;
                margin-bottom: $(num)px !important;
                margin-left: $(num)px !important;
            }
            .u-margin-y-$(size)--$(num) {
                margin-top: $(num)px !important;
                margin-bottom: $(num)px !important;
            }
            .u-margin-x-$(size)--$(num) {
                margin-left: $(num)px !important;
                margin-right: $(num)px !important;
            }

            @each $dir in (top, right, bottom, left) {
                .u-padding-$(dir)-$(size)--$(num) {
                    padding-$(dir): $(num)px !important;
                }
                .u-margin-$(dir)-$(size)--$(num) {
                    margin-$(dir): $(num)px !important;
                }
            }
        }
    }
}


/* Hidden */

.u-hidden,
.u-hidden-s--up,
.u-hidden-xl--down {
    display: none !important;
}

@each $size in $breakpoint-sizes {
    @if $size != s {
        .u-hidden-$(size)--up {
            @media (--bp-$(size)-up) {
                display: none !important;
            }
        }
    }

    .u-hidden-$(size)--only {
        @media (--bp-$(size)-only) {
            display: none !important;
        }
    }

    @if $size != xl {
        .u-hidden-$(size)--down {
            @media (--bp-$(size)-down) {
                display: none !important;
            }
        }
    }
}


/* Navigation */

.c-nav {
    display: flex;
    width: 100%;
    min-height: 6rem;
    line-height: 6rem;

    background-color: var(--nav-background);
    overflow: hidden;

    & p {
        line-height: 6rem;
        padding-left: 3rem;
        padding-right: 3rem;

        font-size: var(--nav-a-font-size);
        color: var(--nav-a-color);
    }

    & a {
        line-height: 6rem;
        padding-left: 3rem;
        padding-right: 3rem;

        font-size: var(--nav-a-font-size);
        color: var(--nav-a-color);
        text-decoration: var(--nav-a-text-decoration);

        &:hover,
        &:active,
        &:visited {
             color: var(--nav-a-color);
             text-decoration: var(--nav-a-text-decoration);
        }

        &.is-active {
             background-color: var(--nav-a-active-background);
             box-shadow: var(--nav-a-active-box-shadow);
        }

        &.c-nav--link {
            @media (--bp-s-only) {
                display: none
            }
        }
    }
}

.c-nav--fixed {
    position: fixed;
    top: 0;
    width: 100%;
    z-index: 1;
}

.c-nav--fixed + .c-container {
    padding-top: 6rem;
}


/* Positioning */

.u-fixed {
    position: fixed;
    index: 2;

    &--top {
        top: 0;
    }

    &--top-right {
        top: 0;
        right: 0;
    }

    &--right {
        right: 0;
    }

    &--bottom-right {
        bottom: 0;
        right: 0;
    }

    &--bottom {
        bottom: 0;
    }

    &--bottom-left {
        bottom: 0;
        left: 0;
    }

    &--left {
        left: 0;
    }

    &--top-left {
        top: 0;
        left: 0;
    }
}

/* Footer */

.c-footer {
    background-color: var(--footer-background);
    overflow: hidden;

    & p {
        color: var(--footer-color);
    }

    & a {
        color: var(--footer-color);
        text-decoration: var(--footer-text-decoration);

        &:hover,
        &:active,
        &:visited {
            color: var(--footer-color);
            text-decoration: var(--footer-text-decoration);
        }
    }
}
